// get asteroid data

let asteroidData = dataObj;

console.log(asteroidData.near_earth_objects);
// create asteroids
let asteroids = [];
let asteroidOrderedData = [];
const asteroidPivots = [
	new THREE.Object3D(),
	new THREE.Object3D(),
	new THREE.Object3D(),
	new THREE.Object3D(),
	new THREE.Object3D(),
	new THREE.Object3D(),
	new THREE.Object3D(),
	new THREE.Object3D(),
];

for (const key in asteroidData.near_earth_objects) {
	let day = [];
	let dayData = [];
	for (let i = 0; i < asteroidData.near_earth_objects[key].length; i++) {
		// Load Earth texture
		const asteroidTexture = new THREE.TextureLoader().load("./asteroid.jpg");

		// Create Earth geometry and material
		let size =
			asteroidData.near_earth_objects[key][i].estimated_diameter.kilometers
				.estimated_diameter_max * 0.05;

		if (size > 0.05) size = 0.05;
		else if (size < 0.01) size = 0.01;

		size = size.toFixed(2);
		const asteroidGeometry = new THREE.SphereGeometry(size, 32, 32);
		const asteroidMaterial = new THREE.MeshBasicMaterial({
			map: asteroidTexture,
		});

		// Create Earth mesh
		const asteroidMesh = new THREE.Mesh(asteroidGeometry, asteroidMaterial);
		const { x, y, z } = getRandomPosition();
		asteroidMesh.position.set(x, y, z);

		day.push(asteroidMesh);
		console.log();
		dayData.push(asteroidData.near_earth_objects[key][i]);
	}
	asteroids.push(day);
	asteroidOrderedData.push(dayData);
	dayData = [];
	day = [];
}

let pivotIdx = 0;
let asteroidIdx = 0;
let currentOutline = null;
document.getElementById("right-arrow-btn").addEventListener("click", () => {
	if (currentOutline) {
		asteroids[pivotIdx][asteroidIdx].remove(currentOutline);
		currentOutline = null;
	}

	if (asteroids[pivotIdx].length - 1 > asteroidIdx) {
		asteroidIdx++;
	} else {
		asteroidIdx = 0;
		pivotIdx++;
	}

	updateUIData();
	updateSelectedFromList();
	updateHighlight();
});
document.getElementById("left-arrow-btn").addEventListener("click", () => {
	if (currentOutline) {
		asteroids[pivotIdx][asteroidIdx].remove(currentOutline);
		currentOutline = null;
	}
	if (asteroidIdx > 0) {
		asteroidIdx--;
	} else {
		if (pivotIdx > 0) {
			asteroidIdx = asteroids[pivotIdx - 1].length - 1;
			pivotIdx--;
		} else {
			pivotIdx = asteroids.length - 1;
			asteroidIdx = asteroids[pivotIdx].length - 1;
		}
	}

	updateUIData();
	updateSelectedFromList();
	updateHighlight();
});

// Define variables
let camera, scene, renderer;
let earthMesh;
let moonMesh;
const geometry = new THREE.SphereGeometry(0.05, 32, 32);
const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
const moonPivot = new THREE.Object3D();

let highlightColor = "rgb(255,0,0)";
let physicsSpeed = 1;

var container = document.getElementById("canvas-area");
function init() {
	// Create a scene
	scene = new THREE.Scene();

	// Create a camera
	camera = new THREE.PerspectiveCamera(
		75,
		window.innerWidth / window.innerHeight,
		0.1,
		1000
	);
	camera.position.z = 5;

	// Create a WebGLRenderer
	renderer = new THREE.WebGLRenderer();
	renderer.setSize(window.innerWidth, window.innerHeight);
	document.body.appendChild(renderer.domElement);
	container.appendChild(renderer.domElement);

	// Load Earth texture
	const earthTexture = new THREE.TextureLoader().load("./texture.jpg");

	// Create Earth geometry and material
	const earthGeometry = new THREE.SphereGeometry(1.5, 32, 32);
	const earthMaterial = new THREE.MeshBasicMaterial({ map: earthTexture });
	// Create Earth mesh
	earthMesh = new THREE.Mesh(earthGeometry, earthMaterial);

	const outlineObject = new THREE.Mesh(
		new THREE.SphereGeometry(1.5, 32, 32),
		new THREE.MeshBasicMaterial({
			color: "rgba(0, 152, 190, 0.6)",
			side: THREE.BackSide,
		})
	);
	outlineObject.material.opacity = 0.1;
	outlineObject.material.transparent = true;
	outlineObject.scale.set(1.1, 1.1, 1.1);
	earthMesh.add(outlineObject);

	const outlineObject1 = new THREE.Mesh(
		new THREE.SphereGeometry(1.5, 32, 32),
		new THREE.MeshBasicMaterial({
			color: "#007c9b",
			side: THREE.BackSide,
		})
	);
	outlineObject1.material.opacity = 0.15;
	outlineObject1.material.transparent = true;
	outlineObject1.scale.set(1.05, 1.05, 1.05);
	earthMesh.add(outlineObject1);
	const outlineObject2 = new THREE.Mesh(
		new THREE.SphereGeometry(1.5, 32, 32),
		new THREE.MeshBasicMaterial({
			color: "#93dbed",
			side: THREE.BackSide,
		})
	);
	outlineObject2.material.opacity = 0.3;
	outlineObject2.material.transparent = true;
	outlineObject2.scale.set(1.025, 1.025, 1.025);
	earthMesh.add(outlineObject2);
	const outlineObject3 = new THREE.Mesh(
		new THREE.SphereGeometry(1.5, 32, 32),
		new THREE.MeshBasicMaterial({
			color: "white",
			side: THREE.BackSide,
		})
	);
	outlineObject3.material.opacity = 1;
	outlineObject3.material.transparent = true;
	outlineObject3.scale.set(1.005, 1.005, 1.005);
	earthMesh.add(outlineObject3);
	// Add Earth to the scene
	scene.add(earthMesh);

	const moonTexture = new THREE.TextureLoader().load("./moon.jpg");
	const moonGeometry = new THREE.SphereGeometry(0.18, 32, 32);
	const moonMaterial = new THREE.MeshBasicMaterial({ map: moonTexture });

	moonMesh = new THREE.Mesh(moonGeometry, moonMaterial);
	earthMesh.add(moonMesh);
	moonMesh.position.set(3, 0, 0);

	moonPivot.add(moonMesh);
	scene.add(moonPivot); // Initialize Three.js

	for (let i = 0; i < asteroids.length; i++) {
		for (let j = 0; j < asteroids[i].length; j++) {
			asteroidPivots[i].add(asteroids[i][j]);
		}
		scene.add(asteroidPivots[i]);
	}
}

// Initialize variables for interpolation
const cameraPosition = new THREE.Vector3(0, 0, 10);
const lookAtPosition = new THREE.Vector3(0, 0, 0);
const easeFactor = 0.1; // Adjust this to control the smoothness of camera movement

// Create an animation loop
function animate() {
	requestAnimationFrame(animate);

	// Rotate the Earth
	earthMesh.rotation.y += 0.002 * physicsSpeed;

	moonPivot.rotation.y += (0.002 / 4) * physicsSpeed; // Adjust the divisor as needed

	asteroidPivots[0].rotation.y += 0.003 * physicsSpeed;
	asteroidPivots[1].rotation.y -= 0.003 * physicsSpeed;
	asteroidPivots[2].rotation.x += 0.004 * physicsSpeed;
	asteroidPivots[3].rotation.x -= 0.003 * physicsSpeed;
	asteroidPivots[4].rotation.z += 0.003 * physicsSpeed;
	asteroidPivots[5].rotation.z -= 0.003 * physicsSpeed;
	asteroidPivots[6].rotation.z += 0.005 * physicsSpeed;
	asteroidPivots[7].rotation.y += 0.004 * physicsSpeed;

	// Render the scene
	renderer.render(scene, camera);
}

// Handle window resizing
function onWindowResize() {
	camera.aspect = window.innerWidth / window.innerHeight;
	camera.updateProjectionMatrix();
	renderer.setSize(window.innerWidth, window.innerHeight);
}

window.addEventListener("resize", onWindowResize);

// Initialize the scene and start the animation loop
init();
animate();

// Initialize OrbitControls
const controls = new OrbitControls(camera, renderer.domElement);
controls.enableDamping = true;
controls.enablePan = false;
controls.dampingFactor = 0.1;

controls.enableZoom = true;

// Update controls on window resize
window.addEventListener("resize", () => {
	const newWidth = window.innerWidth;
	const newHeight = window.innerHeight;
	camera.aspect = newWidth / newHeight;
	camera.updateProjectionMatrix();
	renderer.setSize(newWidth, newHeight);
});

function getRandomPosition() {
	const radius = 3; // Adjust the radius as needed
	const theta = Math.random() * Math.PI * 2;
	const phi = Math.random() * Math.PI;
	const x = radius * Math.sin(phi) * Math.cos(theta);
	const y = radius * Math.sin(phi) * Math.sin(theta);
	const z = radius * Math.cos(phi);
	return { x, y, z };
}

updateUIData();
function updateUIData() {
	let data = asteroidOrderedData[pivotIdx][asteroidIdx];

	let name = asteroidOrderedData[pivotIdx][asteroidIdx].name;
	let magnitude =
		asteroidOrderedData[pivotIdx][asteroidIdx].absolute_magnitude_h;
	let speed =
		asteroidOrderedData[pivotIdx][asteroidIdx].close_approach_data[0]
			.relative_velocity.kilometers_per_second;
	let minSize =
		asteroidOrderedData[pivotIdx][asteroidIdx].estimated_diameter.kilometers
			.estimated_diameter_min;
	let maxSize =
		asteroidOrderedData[pivotIdx][asteroidIdx].estimated_diameter.kilometers
			.estimated_diameter_max;
	let distance =
		asteroidOrderedData[pivotIdx][asteroidIdx].close_approach_data[0]
			.miss_distance.kilometers;
	document.getElementById("name").innerHTML = name;
	document.getElementById("magnitude").innerHTML = magnitude;
	document.getElementById("speed").innerHTML =
		parseFloat(speed).toFixed(4) + " KMPS";
	document.getElementById("min-size").innerHTML =
		parseFloat(minSize).toFixed(8) + " KM";
	document.getElementById("max-size").innerHTML =
		parseFloat(maxSize).toFixed(8) + " KM";
	document.getElementById("cad").innerHTML =
		asteroidOrderedData[pivotIdx][
			asteroidIdx
		].close_approach_data[0].close_approach_date;
	document.getElementById("distance").innerHTML =
		parseFloat(distance).toFixed(2) + " KM";

	if (
		asteroidOrderedData[pivotIdx][asteroidIdx].is_potentially_hazardous_asteroid
	) {
		document.getElementById("threat").innerHTML =
			"Potentially hazardous to Earth";
		document.getElementById("threat").style.color = "red";
	} else {
		document.getElementById("threat").innerHTML = "No threat to Earth";
		document.getElementById("threat").style.color = "#8ff67e";
	}
}
let nightMode = false;
document.getElementById("night-mode-btn").addEventListener("click", () => {
	if (!nightMode) {
		updateEarthTexture("./texture_night.jpg");
		document.getElementById("night-mode-btn").innerHTML =
			"<img src='./moon.png' />";
		nightMode = true;
	} else {
		updateEarthTexture("./texture.jpg");
		document.getElementById("night-mode-btn").innerHTML =
			"<img src='./sun.png' />";
		nightMode = false;
	}
});
function updateEarthTexture(newTexturePath) {
	const newTexture = new THREE.TextureLoader().load(newTexturePath);
	earthMesh.material.map = newTexture;
	earthMesh.material.needsUpdate = true;
}
// populate asteroid list
for (let i = 0; i < asteroidOrderedData.length; i++) {
	for (let j = 0; j < asteroidOrderedData[i].length; j++) {
		document.getElementById(
			"list"
		).innerHTML += `<li pivotIdx="${i}" asteroidIdx="${j}" class="list-item"><p>${
			asteroidOrderedData[i][j].name
		}</p><div class="list-dot" style="background: ${
			asteroidOrderedData[i][j].is_potentially_hazardous_asteroid
				? "red"
				: "#21e102"
		}"></div></li>`;
	}
}

updateSelectedFromList();
function updateSelectedFromList() {
	const listItems = document.querySelectorAll("li");

	// Loop through the list items and set their font color to blue
	listItems.forEach(function (li) {
		li.style.color = "rgb(0, 152, 190)";
	});

	const targetElement = document.querySelector(
		`[pivotIdx="${pivotIdx}"][asteroidIdx="${asteroidIdx}"]`
	);
	targetElement.style.color = "white";
}

updateHighlight();
function updateHighlight() {
	if (currentOutline) {
		asteroids[pivotIdx][asteroidIdx].remove(currentOutline);
		currentOutline = null;
	}

	const outlineMaterial = new THREE.MeshBasicMaterial({
		color: highlightColor, // Outline color
		side: THREE.BackSide, // Render on the backside of the geometry
	});
	const outlineObject = new THREE.Mesh(geometry, outlineMaterial);

	outlineObject.scale.set(1.2, 1.2, 1.2); // Slightly larger than the original object
	asteroids[pivotIdx][asteroidIdx].add(outlineObject); // Add the outline object as a child of the original object
	currentOutline = outlineObject; // Update the current outline object
}

const listItems = document.querySelectorAll(".list-item");
listItems.forEach(function (listItem) {
	listItem.addEventListener("click", function () {
		if (currentOutline) {
			asteroids[pivotIdx][asteroidIdx].remove(currentOutline);
			currentOutline = null;
		}

		const ai = listItem.getAttribute("asteroidIdx");
		const pi = listItem.getAttribute("pivotIdx");
		asteroidIdx = ai;
		pivotIdx = pi;
		updateUIData();
		updateSelectedFromList();
		updateHighlight();
	});
});

